html {
  --color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  min-block-size: 1024px;
  max-block-size: 1200px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  padding-top: 15px;
  display: flex;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.active, .nav__link.is-active {
  color: var(--color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

